* {
	font-size: 12px !important;
}
*,
*::before,
*::after {
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}
input {
	font-size: 12px;
}
#editor canvas {
	display: block;
}
#editor {
	position: relative;
	width: 100%;
	user-select: none;
	-moz-user-select: none;
	-webkit-user-select: none;
	-ms-user-select: none;
}



