#toolbar {
	padding: 8px 0px;
	width: 100%;
	border: 1px solid #e9e9e9;
	height: 42px;
	z-index: 3;
	-webkit-box-shadow: 0px 8px 12px 0px rgba(0, 52, 107, 0.04);
	        box-shadow: 0px 8px 12px 0px rgba(0, 52, 107, 0.04);
	position: absolute;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	overflow: hidden;
}
#toolbar *::before {
	font-size: 16px !important;
}
#toolbar .disable {
	color: rgba(0, 0, 0, 0.25);
}
#toolbar .icon-select.disable {
	background: #eeeeee;
}
#toolbar .separator {
	margin: 4px;
	border-left: 1px solid #e9e9e9;
}
#toolbar .iconfont {
	width: 27px;
	height: 27px;
	margin: 0px 6px;
	border-radius: 2px;
	padding-left: 4px;
	display: inline-block;
	border: 1px solid rgba(2, 2, 2, 0);
}
#toolbar .iconfont:hover {
	cursor: pointer;
	border: 1px solid #e9e9e9;
}
#toolbar .disable:hover {
	cursor: default;
	border: 1px solid rgba(2, 2, 2, 0);
}



